<template>
	<div class="today-infos" >
		<today-infos-item :info-obj="RuKu" path="/warehousingreport">
			<span slot="today-title">今日入库</span>
			<span slot="price-color" class="orange2">￥{{RuKu.today.money}}</span>
		</today-infos-item>
		<today-infos-item :info-obj="ChuKu" path="/shipment">
			<span slot="today-title">今日出库</span>
			<span slot="price-color" class="blue">￥{{ChuKu.today.money}}</span>
		</today-infos-item>
		<today-infos-item :info-obj="CaiGou" path="/purchase">
			<span slot="today-title">今日采购</span>
			<span slot="price-color" class="green">￥{{CaiGou.today.money}}</span>
		</today-infos-item>
		<today-infos-item path="/profitdetail" :info-obj="Sale">
			<span slot="today-title">今日销售</span>
			<span slot="price-color" class="red">￥{{Sale.today.money}}</span>
		</today-infos-item>
	</div>
</template>

<script>

import TodayInfosItem from './TodayInfosItem'

export default {
  name: 'TodayInfosContent',
	components: {
		TodayInfosItem
	},
	data(){
		return{
			RuKu:{
				today:{num:0,money:0},
				yestoday:{num:0,money:0},},
			ChuKu:{
				today:{num:0,money:0},
				yestoday:{num:0,money:0},},
			CaiGou:{
				today:{num:0,money:0},
				yestoday:{num:0,money:0},},
			Sale:{
				today:{num:0,money:0},
				yestoday:{num:0,money:0},
			},
		}
	},
	props:{
		//销售订单
		orderList:{
			type:Object,
			default(){
				return {}
			}
		},
		//入库列表
		warehouseList:{
			type:Object,
			default(){
				return {}
			}
		},
		//采购列表
		purchaseMsg:{
			type:Object,
			default(){
				return {}
			}
		},
		//出库列表
		shipmentMsg:{
			type:Object,
			default(){
				return {}
			}
		},
	},
	methods:{
		//获取今天订单的数据
		getTOdayOrderLength(key,data){
			this[key].today.num=data.length
			this[key].today.money=this.MatchOrderAllMoney(data);
			//console.log(data);
		},
		//获取昨天订单的数据
		getyTOdayOrderLength(key,data){
			this[key].yestoday.num=data.length
			//获取总金额
			this[key].yestoday.money=this.MatchOrderAllMoney(data);
			//console.log(data);
		},
		//中介
		getTOdayOrder(type){
			return this.RuKu[type].list
		},
		//计算总值
		MatchOrderAllMoney(arr){
			if(arr.length<=0)return 0
			let res=0;
			for(let o of arr){
				o.productList.forEach(obj=>{
					res+=obj.price*obj.productnum
				})
			}
			return res.toFixed(2);
		},
		//省略
		returnWatchAttr(key,day){
			return this[key][day].Msg
		},
		getTOdayOrderLength2(key,day,data){
			this[key][day].num=data.length
			this[key][day].money=this.MatchOrderAllMoney2(data)
		},
		//计算属性2
		MatchOrderAllMoney2(arr){
			if(arr.length<=0){return 0}
			let res=0
			arr.forEach(obj=>{
					if(obj.productnum){
						res+=parseInt(obj.productnum)*parseInt(obj.price)
					}else{
						res+=parseInt(obj.num)*parseInt(obj.price)
					}
			})
			return res
		}
	},
	mounted(){
	},
	//计算属性
	computed:{
		todeyOrderLength(){
			return typeof (this.orderList)
		},
		list(){
			return this.RuKu.list
		},
		//给watch监听父组件值变化
		today(){
			return this.returnWatchAttr('orderList','today')
		},
		//给watch监听父组件值变化
		yestoday(){
			return this.returnWatchAttr('orderList','yestoday')
		},
		//给watch监听父组件值变化
		waretoday(){
			return this.returnWatchAttr('warehouseList','today')
		},
		//给watch监听父组件值变化
		wareyestoday(){
			return this.returnWatchAttr('warehouseList','yestoday')
		},
		//给watch监听父组件值变化
		purchaseToday(){
			return this.returnWatchAttr('purchaseMsg','today')
		},
		//给watch监听父组件值变化
		purchaseYestoday(){
			return this.returnWatchAttr('purchaseMsg','yestoday')
		},
		//给watch监听父组件值变化
		shipToday(){
			return this.returnWatchAttr('shipmentMsg','today')
		},
		//给watch监听父组件值变化
		shipYestoday(){
			return this.returnWatchAttr('shipmentMsg','yestoday')
		},
	},
	//监听某个值并且设置
	watch:{
		//今天（监听的是computed的today）
		today(newVal){
			this.getTOdayOrderLength('Sale',newVal)
		},
		//昨天（监听的是computed的yestoday）
		yestoday(newVal){
			this.getyTOdayOrderLength('Sale',newVal)
		},
		//今天（监听的是computed的today）
		waretoday(newVal){
			this.getTOdayOrderLength2('RuKu','today',newVal)
		},
		//昨天（监听的是computed的yestoday）
		wareyestoday(newVal){
			this.getTOdayOrderLength2('RuKu','yestoday',newVal)
		},
		//今天的
		purchaseToday(newVal){
			this.getTOdayOrderLength2('CaiGou','today',newVal)
		},
		//昨天的的
		purchaseYestoday(newVal){
			this.getTOdayOrderLength2('CaiGou','yestoday',newVal)
		},
		//今天的
		shipToday(newVal){
			this.getTOdayOrderLength2('ChuKu','today',newVal)
		},
		//昨天的的
		shipYestoday(newVal){
			this.getTOdayOrderLength2('ChuKu','yestoday',newVal)
		},
	},
	created(){
		//console.log(this.warehouseList.today.Msg.length)
	}
}
</script >

<style spoced>
.today-infos{
}
.orange2{
	color:orange;
}
.red{
	color:#ff6363;
}
.green{
	color:#55b755;
}
.blue{
	color:#7575ff;
}
</style>
